﻿@font-face {
	font-family: 'iconFont';
	src: url('/assets/styles/fonts/iconFont.eot');
	src: url('/assets/styles/fonts/iconFont.eot?#iefix') format('embedded-opentype'), url('/assets/styles/fonts/iconFont.woff') format('woff'), url('/assets/styles/fonts/iconFont.ttf') format('truetype'), url('/assets/styles/fonts/iconfont.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

.iconfont {
	font-family: "iconfont" !important;
	font-size: 16px;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	-webkit-text-stroke-width: 0.2px;
	-moz-osx-font-smoothing: grayscale;
}
